import http from '@/api/http.js';
import md5 from '@/common/md5.js';
import QSPopup from '@/components/QS-popup/QS-popup.vue';
import KeyboardPay from '@/components/pwdpay.vue';
export default {
	components: {KeyboardPay,QSPopup},
	data() {
		return {
			payType:1,
			pointSelect:false,









			chooseList:[
				{name:'微信支付',src:'/static/wx.png',type:1}
			],

			selectImg:'/static/select.png',
			unSelectImg:'/static/unselect.png',
			payList:[],//有效商品
			outPayList:[],//无效商品
			goodsId:0,  // 商品id
			normsId:0, //规格id
			num:0,     // 购买数量
			totalMoney:0,  
			types:0,  // 1为正常下单 2为从购物车下单
			address:{},   
			addressId:0,  //地址id
			haveAddress:false,
			balances:0,   // 余额
			isSetPay:0,   // 0 未设置支付密码   1  设置了
			isPay:false,	//键盘弹出层
			// tixMoney:'',
			cashTitle:'余额支付',
			source:'2',    // 1 提现   2 余额支付
			couponId:0, 	// 优惠券id
			cashList:[],	// 优惠券
			coupon:[],
			cashMoney:0,	// 优惠券钱
			totalFreight:0,   //运费
			member:'',	//是否会员商品
			istequan:'',//是否特权商品
			tequan:1,	//是否特权商品 1普通		 7升级成为农户  商品
			carType:0,	//购物车下单时区分  0普通  1会员
			buycontent:'',//备注
			md5:'',//md5加密
			uid:'',//用户id
			status:true,// 频繁操作状态,
			sec:3,
			yinhkaHTML:'',
			domain_name:'',//域名
			isOut:0,//该地区是否支持配送  1不支持 
		}
	},
	onLoad(option) {
		console.log(option)



		

		let orderdata = wx.getStorageSync('orderdata')

		this.types = orderdata.type;
		if(option.member=='member'){
			this.member = option.member
			this.tequan = 7
		}
		if(option.tequan=='tequan'){
			this.istequan = option.tequan
			this.tequan = 5
			// this.carType=1
		}
		
		if(option.addressid){
			this.addressId = option.addressid;
		}
		this.getdomainName()
		this.chooseSel()
		// this.getPayList()
		// this.getAddress()
		// this.getCash()
		this.getMd5()
		
	},
	onShow() {
		this.getAddress()
		this.getPayList()
	},
	onHide() {
		// this.member=''
	},
	methods:{
		isOuts(){
			// if(this.isOut == 1){
			if(this.payList.length == 0){
				uni.showToast({
					title:'存在该地区不支持配送的商品！',
					icon:'none'
				})
				return
			}else{
				this.show('QSPopupChoose')
			}
		},
		getMd5(){
			let timestap = new Date().getTime()
			this.md5 = md5(timestap + this.uid)
			console.log(this.md5)
		},
		closePayModal(){	
			this.isPay = false;
		},
		getdomainName(){
			http('/Api/setting/downloadInfo','post').then(res=>{
				this.domain_name = res.data.data.domain_name
			})
		},
		useCard(){
			if(this.member=='member'||this.istequan=='tequan'){
				this.$util.showToast('该商品不支持使用优惠券')
				return
			}else{
				this.show('QSPopupTicket')
			}
			
		},
		getCash(){		//优惠券
		// [{"goods_id":"商品id","price":"价格"},{"goods_id":"商品id","price":"价格"}]
			let data = {
				token:uni.getStorageSync('token'),
				coupon_data:JSON.stringify(this.coupon)
			}
			http('/api/User/getCouponList','post',data).then(res=>{
				if(res.data.status == '200'){
					this.cashList = res.data.data.couponList
				}
				console.log(res)
			})
		},
		hideCash(){
			this.hides('QSPopupTicket')
			
		},
		selectedCash(item,i){  //优惠券
			let that = this
			let cartList = that.cashList.map((items, index) => {
				items.checked = false;
				
				if (i == index) {
					that.couponId = items.coupon_id
					that.cashMoney = parseFloat(items.money) 
					items.checked = true;
				}
				return items;
			});
			that.cashList = cartList;
			// that.getPayList()
			that.totalMoney = (that.totalMoney - that.cashMoney).toFixed(2)
		},
		getPayCode(code){   // 余额支付
			let shoplist = [];
			this.payList.forEach(function (item, index) {
				shoplist.push({
					goods_id:item.id,
					attr_id:item.sku_id,
					number:item.number,
					car_type:0
				})
			})
			let payArr = [
				'WeChat',
				'Alipay',
				'cloudFlash',
				'balance'
			]
			if(!this.addressId){
				this.$util.showToast('请选择地址!');
				return false;
			}
			console.log(this.couponId )
			let data = {
				token:uni.getStorageSync('token'),
				type:this.types,			//	1为正常下单 2为从购物车下单 
				data:JSON.stringify(shoplist),
				payment_type:payArr[this.payType-1],	//WeChat 微信 Alipay 支付宝 balance 余额 cloudFlash 云闪付
				address_id:this.addressId,
				payment_password:code,		//余额支付 时候 支付密码
				order_type:this.tequan,    //订单类型 1普通 2团购 3预定 4秒杀 5特权商品 6充值 7会员卡
				coupon_id:this.couponId,  //	优惠券 
				buycontent:this.buycontent
			}
			// http('/api/Order/place_order','post',data).then(res=>{
			http('/api/Order/pay_place_order','post',data).then(res=>{
				if(res.data.status == '200'){
					this.closePayModal()
					this.show('QSPopupSuccess');
					setTimeout(()=>{
						uni.switchTab({
							url:'/pages/mine/mine'
						})
					},1500)
				}else{
					this.$util.showToast(res.data.msg)
					this.closePayModal()
				}
				console.log(res)
			})
		},
		putorder(){			//生成普通订单
			let that = this;
			let timer = ''
			clearInterval(timer);
			timer = setInterval(() => {
			    that.sec--;
			    if ( that.sec <= 0) {
					that.status = true
			        clearInterval(timer);
					that.sec = 3
			    }else{
					that.status = false
				}
			}, 1000)
			if(that.status == false){
				that.$util.showToast('请勿频繁操作!');
				return
			}
			let shoplist = [];
			that.payList.forEach(function (item, index) {
				shoplist.push({
					goods_id:item.id,
					attr_id:item.sku_id,
					number:item.number,
					car_type:0
				})
			})
			let payArr = [
				'WeChat',
				'Alipay',
				'cloudFlash',
				'balance'
			]
			if(!this.addressId){
				this.$util.showToast('请选择地址!');
				return false;
			}
			console.log(payArr[this.payType-1]);

			let data = {
				token:uni.getStorageSync('token'),
				type:this.types,			//	1为正常下单 2为从购物车下单 
				data:JSON.stringify(shoplist),
				payment_type:'Small_WeChat',	//WeChat 微信 Alipay 支付宝 balance 余额 cloudFlash 云闪付
				address_id:this.addressId,
				// payment_password:this.payPassword,		//余额支付 时候 支付密码
				order_type:this.tequan,    //订单类型 1普通 2团购 3预定 4秒杀 5特权商品 7农户卡
				coupon_id:this.couponId,      //	优惠券id
				buycontent:this.buycontent,
				unique_number:this.md5,
				openid:wx.getStorageSync('openid'),
				spread_user_id:uni.getStorageSync('spread_user_id')
			}
			http('/api/Order/pay_place_order','post',data).then(res=>{
				console.log(res)
				if(res.data.status == '200'){
					this.jumpPaysmall(res.data.data.payment);
					
				}else{
					this.$util.showToast(res.data.msg)
					return
				}
				console.log(res)
			})



















































































		},
		jumpPaysmall(resdata){   // 微信 小程序支付
			// appid: "wxbaad9d5bda14b2f6"
			// mch_id: "1560965051"
			// nonce_str: "mvKXAH4CBj8beIDr"
			// package: "prepay_id=wx2617483936509833767334cb1212287400"
			// prepay_id: "wx2617483936509833767334cb1212287400"
			// result_code: "SUCCESS"
			// return_code: "SUCCESS"
			// return_msg: "OK"
			// sign: "694C7D9C00B5478F01F32C6DD7B9397A"
			// time: "1582710519"
			// trade_type: "JSAPI"
			let that = this
			wx.requestPayment({
				'timeStamp': resdata.time,
				'nonceStr': resdata.nonce_str,
				'package': resdata.package,
				'signType': 'MD5',
				'paySign': resdata.sign,
				success:function(res){
					that.$util.hideLoading()
					that.show('QSPopupSuccess');
					that.hides('QSPopupChoose');
					setTimeout(()=>{
						uni.switchTab({
							url:'/pages/mine/mine'
						})
					},1500)
				},
				fail:function(res){
					console.log(111);
					console.log(res);
					that.$util.hideLoading()
					that.$util.showToast('支付失败!');
				},
				complete:function(res){}
			}) 

		},
		jumpPay(resdata){		// 微信  支付宝支付
			console.log(resdata);
			if(this.payType==1){
				console.log(1);
				uni.requestPayment({
					provider: 'wxpay',
					orderInfo:{
						"appid":resdata.appid,
						"noncestr":resdata.nonce_str,
						"package":resdata.package,
						"partnerid":resdata.partnerid,
						"prepayid":resdata.prepay_id,
						"timestamp":parseInt(resdata.time),
						"sign":resdata.sign
					},
					"appid":resdata.appid,
					"noncestr":resdata.nonce_str,
					"package":resdata.package,
					"partnerid":resdata.partnerid,
					"prepayid":resdata.prepay_id,
					"timestamp":parseInt(resdata.time),
					"sign":resdata.sign,
					success: (res) => {
						this.$util.hideLoading()
						this.show('QSPopupSuccess');
						this.hides('QSPopupChoose');
						setTimeout(()=>{
							uni.switchTab({
								url:'/pages/mine/mine'
							})
						},1500)
						
				    },
				    fail: (res) => {
						console.log(111);
						console.log(res);
						this.$util.hideLoading()
						this.$util.showToast('支付失败!');
				    }, 
				    complete: () => {
						
				    }
				})
			}else if(this.payType==2){
				console.log(resdata);
				uni.requestPayment({
					provider: 'alipay',
					orderInfo: resdata,
				    success: (res) => {
						this.$util.hideLoading()
						this.$util.showToast('支付成功!');
						this.show('QSPopupSuccess');
						this.hides('QSPopupChoose');
						setTimeout(()=>{
							uni.switchTab({
								url:'/pages/mine/mine'
							})
						},1500)
				    },
				    fail: (res) => {
						console.log(res);
						this.$util.hideLoading()
						this.$util.showToast('支付失败!');
				    },
				    complete: () => {
				    }
				})
			}
			
		},
		getAddress(){			// 获取地址
			let data = {
				token:uni.getStorageSync('token'),
				address_id:this.addressId
			}
			http('/api/user/getUserAddress','post',data).then(res=>{
				if(res.data.status == '200'){
					this.balances = res.data.data.user.balance
					this.isSetPay = res.data.data.user.is_payment_pass
					this.uid = res.data.data.user.id
					if(res.data.data.address){
						this.address = res.data.data.address;
						this.addressId = res.data.data.address.id;
						
					}
				}
				if(!res.data.data.address){
					this.haveAddress = false
				}else{
					this.haveAddress = true
				}
				console.log(res)
			})
		},
		show(ref) {
			this.$refs[ref].show();
		},
		hides(ref){
			this.$refs[ref].hide()
		},
		pointSelectTap(){
			// this.pointSelect = !this.pointSelect
			this.pointSelect =false
			this.$util.showToast('暂不支持使用积分')
			return
		},
		getPayList(){	
			let that = this;
			let datas = {}










			

			let data = wx.getStorageSync('orderdata')
			datas = {
				...data,
				address_id : this.addressId
			}

			console.log(datas)
			http('/api/Order/create_order','post',datas).then(res=>{
				console.log(1);
				console.log(datas);
				console.log(res)
				if(res.data.status=='200'){
					// that.payList = res.data.data.goods
					that.payList = res.data.data.goods.goods_in_city
					that.outPayList = res.data.data.goods.goods_out_city
					that.isOut = res.data.data.is_out
					that.totalFreight = res.data.data.total_freight
					that.totalMoney = 0
					that.num = 0
					let totalMoneys = 0
					that.payList.forEach(function (item, index) {
						// parseFloat()  parseInt()
						totalMoneys += ((parseFloat(item.number) * (parseFloat(item.price)*100))/100)
						that.coupon.push({goods_id:item.id,price:totalMoneys})
						that.totalMoney = (totalMoneys + that.totalFreight).toFixed(2)
						that.num += parseInt(item.number) 
						// if(!item.car_type){
						// 	that.$set(item,'car_type',that.carType)
						// }
						// if(item.car_type==1 && that.istequan==''){
						// 	that.tequan=7
						// }
					})
					that.getCash()
					console.log(that.coupon)
				}else{
					that.$util.showToast(res.data.msg)
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},2000)
				}
			})
			
		},
		choose(type, indexs) {		//选择支付方式
			let that = this
			let cartList = that.chooseList.map((item, index) => {
				item.checked = false;
				
				if (indexs == index) {
					that.payType = item.type
					item.checked = true;
				}
				return item;
			});
			that.chooseList = cartList;
			
		},
		chooseSel(){
			let cartList = this.chooseList.map((item, index) => {
				item.checked = false;
				if (index == 0) {
					item.checked = true;
				}
				return item;
			});
		},
		chooses(type, indexs){
			let that = this
			let cartList = that.chooseListwx.map((item, index) => {
				item.checked = false;
				
				if (indexs == index) {
					that.payType = item.type
					item.checked = true;
				}
				return item;
			});
			that.chooseListwx = cartList;
		},
		toAddress(){
			uni.navigateTo({
				url:'/pages/index/address/address?changeType=1&member='+this.member+'&tequan='+this.istequan
			})
		}
	}
}
